<template>
  <div class="nine">

    <h1>NineIo Test</h1>

    <b-card
    header="axios test"
    style="max-width: 40rem; margin: auto;"
    >
      <b-textarea v-model="b1" placeholder= "get: 1 or 2, post: 3 or 4"></b-textarea>
      <b-btn @click="btn">1 or 2, GetClick</b-btn>
      <p>{{a}}</p>
      <b-btn @click="postBtn">3 or 4, PostClick</b-btn>
      <p>{{c}}</p>
    </b-card>


  </div>
</template>


<script>
export default {
  name: 'NineIo',
  props: {
    msg: String
  },
  data: ()=>({
    b1:'',
    a:'',
    c:''
  }),
  methods:{
    btn(){
      this.axios.get('http://localhost:9000/api/axiosTest', {params:{tEst:this.b1}})
          .then(result=>{
            console.log(result.data)
            this.a = result.data
          })
    },
    postBtn(){
      const fd = new FormData()
      fd.append('demo', this.b1);
      this.axios.post('http://localhost:9000/api/axiosPostTest', fd)
      .then(result=>{
        console.log(result.data);
        this.c = result.data;
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>
